/* Books section */
.books-section {
    margin: 2rem 0;
    padding: 0 1rem; /* Add padding for consistency */
}

.book-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 1rem;
}

/* Book Item */
.book-item {
    flex: 1 1 calc(25% - 1rem);
    text-align: center;
    margin-bottom: 2rem;
    background-color: #fff;
    padding: 1rem;
    border: 1px solid #ddd;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
    max-width: 250px;
}

.book-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
}

.book-item:hover {
    transform: translateY(-5px);
}

.book-item img {
    height: 220px;
    width: auto;
    display: block;
    margin-bottom: 1rem;
}

.book-item h3 {
    margin: 0.5rem 0;
    font-size: 1.1rem;
    color: #333;
    height: 50px;
}

.price {
    color: red;
    font-weight: bold;
    margin: 0.5rem 0;
    font-size: 1rem;
}

.button {
    margin-top: 0.5rem;
    padding: 0.7rem 1.5rem;
    border-radius: 20px;
    border: none;
    cursor: pointer;
    width: 80%;
    font-size: 16px;
    text-align: center;
    box-sizing: border-box;
    display: inline-block;
    max-width: 200px;
    transition: background-color 0.3s ease, color 0.3s ease;
}

/* Shop Now button specific styles */
.shop-now-button {
    background-color: #ff6347;
    color: white;
}

.shop-now-button:hover {
    background-color: #e5533f;
    color: white;
}

.read-now-button {
    background-color: #f2250a;
    color: white;
    margin-top: 0.5rem;
}

.read-now-button:hover {
    background-color: #ad200e;
}

/* Add to Cart button specific styles */
.add-to-cart-button {
    background-color: #e0e0e0;
    color: black;
}

.add-to-cart-button:hover {
    background-color: #ccc;
    color: black;
}

/* Align and set consistent spacing between buttons */
.shop-now-button, .cart-button {
    margin-bottom: 0.5rem;
}

/* Category Section */
.category-section {
    margin: 2rem 0;
    text-align: center;
}

.category-section h1 {
    font-size: 32px;
    margin-bottom: 1rem;
}

.category-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 1rem;
}

.book-item {
    flex: 1 1 calc(25% - 1rem);
    text-align: center;
    margin-bottom: 2rem;
    max-width: 250px;
}

.book-item img {
    height: 200px;
    width: auto;
    display: block;
    margin: 0 auto;
}

.price {
    color: red;
    font-weight: bold;
    margin-top: 0.5rem;
}

.cart-button {
    margin-top: 1rem;
    padding: 0.5rem 1.5rem;
    background-color: transparent;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.cart-count {
    position: absolute;
    top: -5px;
    right: -5px;
    background-color: #ff6347; /* Red background for visibility */
    color: white;
    border-radius: 50%;
    padding: 0.2rem 0.5rem;
    font-size: 12px;
    font-weight: bold;
    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cart-button:hover {
    background-color: #ccc;
    color: #000;
}